import FinanceTracker from "@/components/finance-tracker"
import { getTransactions, getTransactionStats } from "@/app/actions/transactions"
import { convertDateFromISO } from "@/lib/api-utils"

export default async function Home() {
  // Fetch initial data on the server
  // Default to December 2025 (ডিসেম্বর ২০২৫)
  const initialMonth = "ডিসেম্বর ২০২৫"
  const transactionsResult = await getTransactions({ month: initialMonth })
  const statsResult = await getTransactionStats(initialMonth)

  // Convert dates from ISO to dd/MM/yyyy format for display
  const initialTransactions =
    transactionsResult.success && transactionsResult.data
      ? transactionsResult.data.map((t) => ({
          ...t,
          date: convertDateFromISO(t.date),
        }))
      : []

  const initialTotals =
    statsResult.success && statsResult.data
      ? {
          income: statsResult.data.totals.income,
          expense: statsResult.data.totals.expense,
          savings: statsResult.data.totals.savings,
        }
      : { income: 0, expense: 0, savings: 0 }

  return (
    <main className="min-h-screen bg-background">
      <FinanceTracker
        initialTransactions={initialTransactions}
        initialTotals={initialTotals}
        initialMonth={initialMonth}
      />
    </main>
  )
}
